<template>
  <div>
    <ul class="list-group">
      <li class="list-group-item" v-for="(section, index) in sectionList" :key="index">
        <router-link 
          :to="'/' + section.md">
          {{ section.name }}
        </router-link>
      </li>
    </ul>  
    <router-view></router-view>
  </div>
</template>

<script>

/* eslint-disable */

export default {
  name: 'app',
  data() {
    return {
      sectionList : {}
    }
  },
  mounted() {
    this.getMDfromServer()
  },
  methods:{
    getMDfromServer(){
      return $.get(
        "https://raw.githubusercontent.com/testissimo/testissimo.github.io/master/static/json/sitemap.json", 
        (data, status)=> this.loadSections(data, status))
    },
    loadSections(data, status){
      this.sectionList = JSON.parse(data);
    }
  }
}
</script>

