<template>
    <div id="showdownDiv">
        <VueShowdown :markdown="mdData"/>
    </div>

</template>

<script>

/* eslint-disable */

import Vue from 'vue'
import { VueShowdown } from 'vue-showdown'


Vue.component('VueShowdown', VueShowdown) 

export default {
    name : 'DocumentationContent',
    data() {
        return {
            mdData : "Loading MDdata"
        }
    },
    methods : {
        loadFile(id){
            var mdFromServer;
            var path = "https://raw.githubusercontent.com/testissimo/testissimo.github.io/master/static/md/" + id + ".md"
            $.get(path, function (data, status){
                mdFromServer = data;
            })
            .then(()=>{
                this.mdData = mdFromServer;
            })
            .catch((error)=>{
                console.log(error.message, error)
                this.mdData = "The data you requested could not be found!"
            })
        }
    },
    watch: {
        $route (){
            this.loadFile(this.$route.params.id)
        }
    },
    beforeMount() {
        console.log('i got this here', this.$route)
        this.loadFile(this.$route.params.id)
    },
}
</script>

